<template>
    <div id="app">
        <Tool
            :set_painter_code="set_painter_code"
            :paint_clear="paint_clear"
            :random="random"
            :show="showChinese"
            :set_random="set_random"
            :set_show="set_show"
        />
        <div class="main">
            <div>
                <p>[z]切换显示 [r]切换顺序 [a][d][←][→]翻页</p>
                <p>设备页面宽度: {{ width }}</p>
            </div>

            <PageBtn :page="page" :pageMax="pageMax" :set_page="set_page" />

            <div class="words">
                <WordContainer
                    :words="showWords[0]"
                    :showChinese="showChinese"
                    :painterCode="painterCode"
                    :upload_words="upload_words"
                />
                <WordContainer
                    :words="showWords[1]"
                    :showChinese="showChinese"
                    :painterCode="painterCode"
                    :upload_words="upload_words"
                />
            </div>

            <PageBtn :page="page" :pageMax="pageMax" :set_page="set_page" />
        </div>
    </div>
</template>

<script>
import PageBtn from "@/components/PageBtn";
import Tool from "@/components/Tool";
import WordContainer from "@/components/WordContainer";
export default {
    name: "App",
    data() {
        return {
            page: 0,
            pageMax: 0,
            words: [],
            showChinese: true,
            width: 0,
            painterCode: 0,
            random: false,
        };
    },
    components: { PageBtn, Tool, WordContainer },
    created() {
        var temp = require("./word.json");
        temp.forEach((words_page_t) => {
            var words_page_d = [];
            words_page_t.forEach((words_col_t) => {
                var words_col_d = [];
                Object.keys(words_col_t).forEach((e) => {
                    words_col_d.push({
                        key: e,
                        val: words_col_t[e],
                        group: 0,
                    });
                });
                words_page_d.push(words_col_d);
            });
            this.words.push(words_page_d);
        });
        this.pageMax = this.words.length - 1;
    },
    mounted() {
        document.addEventListener("keyup", this.keyupEvent);
        window.onresize = () => {
            return (() => {
                this.width = document.body.clientWidth;
            })();
        };
        this.width = document.body.clientWidth;

        // 从本地缓存读取
        var temp = localStorage.getItem("words");
        if (temp) this.words = JSON.parse(temp);

        temp = localStorage.getItem("pageIndex");
        if (temp) this.page = +temp;
    },
    destroyed() {
        document.removeEventListener("keyup", this.keyupEvent);
    },
    methods: {
        keyupEvent(event) {
            console.log(event);
            if (event.key == "z") {
                this.showChinese = !this.showChinese;
            } else if (event.key == "a" || event.key == "ArrowLeft") {
                this.page = Math.max(0, this.page - 1);
            } else if (event.key == "d" || event.key == "ArrowRight") {
                this.page = Math.min(this.pageMax, this.page + 1);
            } else if (event.key == "r") {
                this.random = !this.random;
            }
        },
        set_page(val) {
            this.page = val;
            localStorage.setItem("pageIndex", this.page);
        },
        set_painter_code(val) {
            this.painterCode = val;
        },
        set_random(val) {
            this.random = val;
        },
        set_show(val) {
            this.showChinese = val;
        },
        paint_clear() {
            this.words[this.page].forEach((e) => {
                e.forEach((e) => {
                    e.group = 0;
                });
            });
            this.upload_words();
        },
        upload_words() {
            localStorage.setItem("words", JSON.stringify(this.words));
            console.log("保存到本地缓存");
        },
        shuffle(arr) {
            var len = arr.length;
            for (var i = 0; i < len - 1; i++) {
                var index = parseInt(Math.random() * (len - i));
                var temp = arr[index];
                arr[index] = arr[len - i - 1];
                arr[len - i - 1] = temp;
            }
            return arr;
        },
    },
    computed: {
        showWords() {
            var a = this.words[this.page];
            if (this.random) {
                var b = [];
                a.forEach((e) => {
                    b.push(this.shuffle(e.slice()));
                });
                return b;
            }
            return a;
        },
    },
};
</script>

<style>
#app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    margin-top: 60px;
    display: flex;
    justify-content: center;
}
.main {
    display: grid;
    grid-template-rows: repeat(4, auto);
    width: auto;
    justify-items: center;
    align-items: center;
}

.words {
    display: flex;
    flex-wrap: wrap; /* 方便手机端适配 */
    justify-content: center;
}
</style>
