<template>
    <div class="container">
        <button class="func-btn" @click="switch_random">
            {{ randomNextStatus }}
        </button>
        <button class="func-btn" @click="switch_show">
            {{ showNextStatus }}
        </button>
        <button class="func-btn" @click="switch_painter_container">
            {{ painterNextStatus }}
        </button>
        <div class="painter-container" v-show="painterNextStatus == '关闭'">
            <button
                v-for="(item, index) in painters"
                :key="index"
                :class="
                    'painter ' +
                    item.color +
                    (painterCode == item.code ? ' show' : '')
                "
                @click="set_code(item.code)"
            >
                {{ item.name }}
            </button>
        </div>
    </div>
</template>

<script>
export default {
    name: "Tool",

    data() {
        return {
            painterCode: 0,
            painters: [
                {
                    color: "red",
                    name: "红",
                    code: 1,
                },
                {
                    color: "blue",
                    name: "蓝",
                    code: 2,
                },
                {
                    color: "green",
                    name: "绿",
                    code: 3,
                },
                {
                    color: "white",
                    name: "清除本页",
                    code: 0,
                },
            ],
            painterShow: true,
        };
    },

    props: [
        "set_painter_code",
        "paint_clear",
        "random",
        "show",
        "set_random",
        "set_show",
    ],

    mounted() {
        var temp = localStorage.getItem("painterCode");
        if (temp) this.painterCode = +temp;
    },
    methods: {
        set_code(val) {
            if (val == 0) this.paint_clear();
            if (this.painterCode == val) val = 0;
            this.painterCode = val;
            this.set_painter_code(val);
            localStorage.setItem("painterCode", this.painterCode);
        },
        switch_painter_container() {
            this.painterShow = !this.painterShow;
        },
        switch_random() {
            this.set_random(!this.random);
        },
        switch_show() {
            this.set_show(!this.show);
        },
    },
    computed: {
        randomNextStatus() {
            if (this.random) return "顺序";
            else return "乱序";
        },
        showNextStatus() {
            if (this.show) return "纯英";
            else return "中英";
        },
        painterNextStatus() {
            if (this.painterShow) return "关闭";
            else return "打开";
        },
    },
};
</script>

<style scoped>
.container {
    position: fixed;
    right: 10px;
    display: flex;
    flex-direction: column;
    bottom: 20%;
    width: 70px;
}

.func-btn {
    height: 40px;
}

.painter-container {
    position: relative;
    display: flex;
    flex-direction: column;
}

.painter {
    height: 70px;
    width: 70px;
    font-size: 20px;
    border: solid 1px;
    opacity: 0.3;
}

.show {
    opacity: 1;
}

.red {
    color: white;
    background-color: red;
}
.blue {
    color: white;
    background-color: blue;
}
.green {
    color: white;
    background-color: green;
}
.white {
    background-color: white;
}
</style>